import React from "react";
import { useParams } from "react-router-dom";
import { Grid, makeStyles } from "@material-ui/core";

import Hero from "../models/Hero";
import Item from "../models/Item";
import PartyStatistics from "../models/PartyStatistics";
import WorldStatistics from "../models/WorldStatistics";

import MockHeroService from "../services/MockHeroService";
import MockPartyService from "../services/MockPartyService";
import MockWorldService from "../services/MockWorldService";
import MockItemService from "../services/MockItemService";
import StringConverters from "../services/StringConverters";

import HeroHelper from "../helpers/HeroHelper";

import PartyInventory from "../components/PartyInventory";
import EquippedItems from "../components/EquippedItems";
import StatisticsBlock from "../components/StatisticsBlock";
import CalculatedStatisticsBlock from "../components/CalculatedStatisticsBlock";
import TraitList from "../components/TraitList";

const useStyles = makeStyles((theme) => ({
    characterGrid: {
        backgroundColor: "beige",
        fontFamily: "Martel",
        display: "grid",
        gridAutoRows: "minmax(100px, auto)",
        height: "91.5vh",
        [theme.breakpoints.down("md")]: {
            gridTemplateColumns: "repeat(1, 1fr)",
        },
        [theme.breakpoints.up("md")]: {
            gridTemplateColumns: "repeat(2, 1fr)",
        },
    },
    left: {},
    sectionHeading: {
        fontFamily: "MedievalSharp",
    },
    bannerContainer: {
        position: "relative",
        height: "15%",
        top: "-80px",
        zIndex: 1,
    },
    itemContainer: {
        position: "relative",
    },
    close: {
        position: "absolute",
        top: "10.2%",
        left: "1%",
        width: "40px",
        zIndex: 2,
        filter: "invert(42%) sepia(93%) saturate(1352%) hue-rotate(32deg) brightness(60%) contrast(119%)",
    },
    portrait: {
        backgroundColor: "ivory",
        height: "56vh",
        zIndex: 0,
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    portraitImg: {
        //left: "20%",
        width: "18vw",
        border: "solid",
        borderRadius: "50%",
        borderWidth: "10px",
        borderColor: "lightslategrey",
        margin: "28px",
    },
    inventory: {
        position: "relative",
        height: "34vh",
        backgroundColor: "ivory",
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    statistics: {
        overflowY: "scroll",
        backgroundColor: "ivory",
        gridRowEnd: "span 2",
        border: "solid",
        borderWidth: "4px",
        borderColor: "beige",
        borderRadius: "20px",
    },
    statisticsBlock: {
        padding: "40px 80px",
    },
}));

type HeroProfileProps = {
    //id: string,
};

const HeroProfile: React.FunctionComponent = () => {
    const classes = useStyles();

    const params: any = useParams();
    const id = params.id;

    let [hero, setHero] = React.useState(null as Hero | null);
    let [party, setParty] = React.useState(null as PartyStatistics | null);
    let [world, setWorld] = React.useState(null as WorldStatistics | null);
    let [equippedItems, setEquippedItems] = React.useState(null as Item[] | null);
    let [inventoryItems, setInventoryItems] = React.useState(null as Item[] | null);
    let [currentId, setCurrentId] = React.useState(-1);

    React.useEffect(() => {
        // Initial load of hero data
        const loadHero = () => {
            const service = new MockHeroService();
            const newHero = service.getHeroDetail(id);

            const partyService = new MockPartyService();
            const newParty = partyService.getPartyStatistics();

            const worldService = new MockWorldService();
            const newWorld = worldService.getWorld();

            const itemsService = new MockItemService();
            const newItems = itemsService.getItems(newHero.equippedItems);

            if (!newHero) {
                console.error(`No hero data`);
                return;
            }
            if (!newParty) {
                console.error(`No party data`);
                return;
            }
            if (!newWorld) {
                console.error(`No world data`);
                return;
            }
            if (!newItems) {
                console.error(`No items data`);
                return;
            }

            console.log("Loading hero");

            setCurrentId(id);
            setHero(newHero);
            setParty(newParty);
            setWorld(newWorld);
            setEquippedItems(newItems);
        };

        // Initial load of inventory
        const loadItems = () => {
            // TODO: Try / catch
            const partyService = new MockPartyService();
            const inventoryItemIds = partyService.getInventory();

            const itemService = new MockItemService();
            const newInventoryItems = itemService.getItems(inventoryItemIds);

            setInventoryItems(newInventoryItems);
        };

        if (inventoryItems === null) {
            loadItems();
        }

        if (currentId !== id) {
            loadHero();
        }
    });

    if (!hero || !party || !world || !equippedItems || !inventoryItems) return null;

    const onEquipItem = (itemId: string) => {

        // console.log(`hero '${id}' equips ${itemId}`);

        // Equip the item
        const heroService = new MockHeroService();
        const removedItemId = heroService.equipItem(id, itemId);

        // Remove the item from party inventory
        const partyService = new MockPartyService();
        partyService.removeInventoryItem(itemId);
        if (removedItemId) {
            partyService.addInventoryItem(removedItemId);
        }

        // Force a refresh
        setCurrentId(-1);
        setInventoryItems(null);
    };

    return (
        <div className={classes.characterGrid}>
            <div className={classes.left}>
                <div className={classes.portrait}>
                    <br />
                    <a href="/#/">
                        <img src="images/close.svg" className={classes.close} alt="close" />
                    </a>
                    <div className={classes.bannerContainer}>
                        <h1 className="banner">{hero.name}</h1>
                    </div>
                    <div className={classes.itemContainer}>
                        <p>
                            {StringConverters.classToString(hero.class)} - {StringConverters.alignmentToString(hero.alignment)}
                        </p>
                        <Grid container>
                            <Grid item xs={3}>
                                <EquippedItems equippedItems={equippedItems} />
                            </Grid>
                            <Grid item xs={6}>
                                <img src={hero.imageUrl} className={classes.portraitImg} alt="profile" />
                            </Grid>
                            <Grid item xs={3}></Grid>
                        </Grid>
                    </div>
                </div>
                <div className={classes.inventory}>
                    <br />
                    <h1 className={classes.sectionHeading}>Party Inventory</h1>
                    <PartyInventory items={inventoryItems} onEquipItem={onEquipItem} />
                </div>
            </div>
            <div className={classes.statistics}>
                <br />
                <div className={classes.statisticsBlock}>
                    <p>{hero.background}</p>
                </div>
                <br />
                <h1 className={classes.sectionHeading}>Statistics</h1>
                <div className={classes.statisticsBlock}>
                    <StatisticsBlock statistics={HeroHelper.calculateAttributesWithModifiers(hero)} />
                </div>
                <h1 className={classes.sectionHeading}>Other</h1>
                <CalculatedStatisticsBlock statistics={HeroHelper.calculateStatsWithModifiers(hero)} />
                <h1 className={classes.sectionHeading}>Abilities</h1>
                <div className={classes.statisticsBlock}>
                    <p>{StringConverters.abilitiesToString(hero.abilities)}</p>
                </div>
                <h1 className={classes.sectionHeading}>Traits</h1>
                <TraitList traits={HeroHelper.getAllTraits(hero)} />
                <h1 className={classes.sectionHeading}>Party Standing</h1>
                <div className={classes.statisticsBlock}>
                    <p>Reputation {party.reputation}</p>
                    {party.traits.map((t) => (
                        <p>{t}</p>
                    ))}
                </div>
                <h1 className={classes.sectionHeading}>Voormithadreth Status</h1>
                <div className={classes.statisticsBlock}>
                    {world.traits.map((t) => (
                        <p>{t}</p>
                    ))}
                </div>
            </div>
        </div>
    );
}

export default HeroProfile;
