import React from 'react';
import { useHistory } from "react-router-dom";
import { Card, CardHeader, CardContent, Grid, makeStyles } from "@material-ui/core";

const useStyles = makeStyles({
    root: {
        backgroundColor: "#282c34",
        height: "100vh",
    },
    close: {
        position: "absolute",
        top: "20px",
        left: "20px",
        width: "40px",
        zIndex: 2,
        filter: "invert(60%)",
    },
    container: {
        top: "8vh",
        position: "relative",
    },
    titleContainer: {
        padding: "10vh",
    },
    appLogo: {
        height: "8vh",
        pointerEvents: "none",
        padding: "20px",
    },
    card: {
        position: "relative",
        margin: "40px",
        cursor: "pointer",
        "&:hover": {
            top: "-5px",
            right: "5px",
            boxShadow: "10px 10px",
        },
    },
});

const MainMenu: React.FunctionComponent = () => {

    const classes = useStyles();

    let history = useHistory();

    const onExit = () => {
        const remote = require("electron").remote;
        if (remote) { 
            const window = remote.getCurrentWindow();
            window.close();
        }
        else {
            history.push('/');
        }
    }
    
    return (
        <div className={classes.root}>
            <a href="/#/">
                <img src="images/close.svg" className={classes.close} alt="close" />
            </a>
            <div className={classes.titleContainer}>
                <img src="./volcano.svg" className={classes.appLogo} alt="logo" />
                <img className="title-large shadow" alt="Tales from the Pit" src="title.svg" />
            </div>
            <Grid container className={classes.container}>
                <Grid item xs={4}></Grid>
                <Grid item xs={4}>
                    <Card className={classes.card}>
                        <CardHeader title="New Game" />
                        <CardContent>Start a new game</CardContent>
                    </Card>
                    <Card className={classes.card} onClick={onExit}>
                        <CardHeader title="Quit" />
                        <CardContent>Exit the game</CardContent>
                    </Card>
                </Grid>
                <Grid item xs={4}></Grid>
            </Grid>
        </div>
    );
}

export default MainMenu;
